  position: relative;
  cursor: pointer;
  top: -3;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #cccccc;
  -webkit-transition: 0.4s;
  transition: 0.4s; }

.slider-switch:before {
  position: absolute;
  content: "";
  height: 12px;
  width: 12px;
  left: 4px;
  bottom: 3.5px;
  background-color: #fff;
  -webkit-transition: 0.4s;
  transition: 0.4s; }

input:checked + .slider-switch {
  background-color: #009c33; }

