  box-shadow: 0 0 1px #2196f3; }

input:checked + .slider-switch:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px); }

.slider-switch.round {
  border-radius: 34px; }

.slider-switch.round:before {
  border-radius: 50%; }

